/* Ensure this is in your existing CSS file or adjust as needed */
.ngo-showcase {
    padding: 20px;
  }
  
  .logo-container {
    margin-bottom: 20px;
  }
  
  .ngo-logo {
    max-width: 100%; /* Ensure the logo scales down with the container */
    height: auto;
  }
  
  .ngo-title {
    font-size: 2rem;
  }
  
  .ngo-description {
    font-size: 1.1rem;
    line-height: 1.6;
  }
  
  /* Additional media queries for custom breakpoints if needed */
  @media (max-width: 768px) {
    .ngo-title {
      font-size: 1.5rem;
    }
  
    .ngo-description {
      font-size: 1rem;
    }
  }
  