@font-face {
  font-family: Icons;
  src: url(../Fonts/open-iconic/open-iconic.eot);
  src: url(../Fonts/open-iconic/open-iconic.eot?#iconic-sm)
      format("embedded-opentype"),
    url(../Fonts/open-iconic/open-iconic.woff) format("woff"),
    url(../Fonts/open-iconic/open-iconic.ttf) format("truetype"),
    url(../Fonts/open-iconic/open-iconic.otf) format("opentype"),
    url(../Fonts/open-iconic/open-iconic.svg#iconic-sm) format("svg");
  font-weight: 400;
  font-style: normal;
}
.oi {
  position: relative;
  top: 1px;
  display: inline-block;
  speak: none;
  font-family: Icons;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.oi:empty:before {
  width: 1em;
  text-align: center;
  box-sizing: content-box;
}
.oi.oi-align-center:before {
  text-align: center;
}
.oi.oi-align-left:before {
  text-align: left;
}
.oi.oi-align-right:before {
  text-align: right;
}
.oi.oi-flip-horizontal:before {
  -webkit-transform: scale(-1, 1);
  -ms-transform: scale(-1, 1);
  transform: scale(-1, 1);
}
.oi.oi-flip-vertical:before {
  -webkit-transform: scale(1, -1);
  -ms-transform: scale(-1, 1);
  transform: scale(1, -1);
}
.oi.oi-flip-horizontal-vertical:before {
  -webkit-transform: scale(-1, -1);
